import { Component } from "react";
class Button extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { title2, butcon, ...props } = this.props;
    return (
      <button className={butcon} {...props}>
        {title2} and {this.props.children}
      </button>
    );
  }
}

export default Button;
