import { Component } from "react";
import Input from "./input";
class RegisterForm extends Component {
  state = { pl: "This Placeholder is from State" };
  
  render() {
    
    return (
      <form className="registerfont">
        <span>ResgisterForm</span>
        <Input
          firstspan="HiiiiiSpan" 
          spanStyle={"inputStyle"}
          placeholder={this.state.pl}
          onChange={(e) => {
            console.log(e.target.value);
          }}
        />
      </form>
    );
  }
}

export default RegisterForm;
