import { Component } from "react";
class Input extends Component {
  constructor(props) {
    super(props);
  }
  state = {};
  render() {
    const { spanStyle, firstspan, hei, ...props } = this.props;
    return (
      <label {...props} style={{display:"flex",flexDirection:"column"}}>
        {spanStyle}
        <span style={{margin:"30px",backgroundColor:'red'}}>
          <br />
          {firstspan}
        </span>
        <input className={spanStyle} {...props} />
      </label>
    );
  }
}

export default Input;
