import { Component } from "react";
import Input from "./input";
import Button from "./Button";
import "./css/loginForm.css";
import { IonIcon } from "@ionic/react";
class LoginForm extends Component {
  state = { ti: "tiiii", pass: "pa", showPass: "false" };
  toggleeye() {}
  render() {
    return (
      <form
        className={"login-form-container"}
        onSubmit={(e) => {
          e.preventDefault();
        }}
      >
        <span>login form</span>
        <Input
          className={"displayinput"}
          style={{ backgroundColor: "yellow", margin: "10px 20px" }}
          placeholder={"placeholdermo"}
          onChange={(e) => {
            console.log(e.target.value);
          }}
        />
        <div style={{ display: "flex", backgroundColor: "grey" }}>
          {" "}
          <Input
            firstspan={this.state?.pass}
            type={this.state.showPass === "true" ? "text" : "password"}
            onChange={(e) => {
              console.log(e.target.value);
            }}
          />
          <ion-icon name="heart"></ion-icon>
          <i
            class={
              this.state.showPass === "true"
                ? "fa-solid fa-eye"
                : "fa-solid fa-eye-slash"
            }
            style={{ margin: "30px" }}
            onClick={() => {
              this.state.showPass === "true"
                ? this.setState({ showPass: "false" })
                : this.setState({ showPass: "true" });
              console.log(this.state.showPass);
            }}
          ></i>
        </div>
        <Button
          butcon={"button-container"}
          title2={this.state.ti}
          onClick={() => {
            this.setState({ ti: "Changed State" });
          }}
        >
          Button From button children
        </Button>
      </form>
    );
  }
}

export default LoginForm;
